import { render, unmountComponentAtNode } from 'react-dom';
import {
    EuiPanel,
    EuiSpacer,
    EuiKeyPadMenuItem,
    EuiIcon,
    EuiFlexGroup,
    EuiFlexItem,
    EuiText,
    EuiTitle,
    EuiSwitch,
    EuiImage,
    EuiBetaBadge,
    EuiRadio,
    EuiFlexGrid,
    EuiModal,
    EuiModalHeader,
    EuiModalHeaderTitle,
    EuiModalBody,
    EuiModalFooter,
    EuiButtonEmpty,
    EuiFieldText
} from '@elastic/eui';
import { useBoolean, useSafeState, useSetState, useUpdateEffect } from 'ahooks';
import _ from 'lodash';
import domtoimage from 'dom-to-image';

import './index.scss';
import Styles from './index.scss';
import Logo from '@assets/images/logo.png';

import { BLACK, WHITE, NORMAL, CORE } from '@modules/constants';
import { usePlayerCtrl, useRunesGenerator } from '@modules/control';
import {
    getNewBoard,
    targetParser,
    ownerParser,
    scoreParser,
    deployTargetParser,
    coreBackParser,
    upgradeParser,
    coreAllocParser
} from '@modules/parser';
import { downloadFile, uploadFile } from '@modules/utils';
import ScoreBar from '@components/scoreBar';
import Timer from '@components/timer';
import Piece from '@components/piece';
import Rune from '@components/rune';
import Upgrader from '@components/upgrader';

const { ClipboardItem } = window;

const newBoard = {
    blackRunes: [null, null, null],
    board: [
        [null, null, null, null, null, null, null],
        [null, null, null, null, null, { owner: BLACK, type: CORE, upgrade: NORMAL }, null],
        [null, null, null, null, null, null, null],
        [null, null, null, null, null, null, null],
        [null, null, null, null, null, null, null],
        [null, { owner: WHITE, type: CORE, upgrade: NORMAL }, null, null, null, null, null],
        [null, null, null, null, null, null, null]
    ],
    whiteRunes: [null, null, null]
};

const getRunePlayer = (index) => {
    const blackRuneIndex = [-1, -2, -3];
    const whiteRuneIndex = [-4, -5, -6];

    if (blackRuneIndex.includes(index)) {
        return BLACK;
    } else if (whiteRuneIndex.includes(index)) {
        return WHITE;
    } else {
        return null;
    }
};

const HomePage = () => {
    const [curPlayer, togglePlayer] = usePlayerCtrl();
    const [blackScore, setBlackScore] = useSafeState(0);
    const [whiteScore, setWhiteScore] = useSafeState(0);
    const [done, doneCtl] = useBoolean(false);

    const [activePos, setActivePos] = useSafeState(null);
    const [ptsMatrix, setPtsMatrix] = useSafeState(getNewBoard(0));
    const [targetMatrix, setTargetMatrix] = useSafeState(getNewBoard(false));
    const [globalBoard, setGlobalBoard] = useSetState(newBoard);
    const [shuffleRunes, getShuffleRunes, setShuffleRunes] = useRunesGenerator();

    const [playerName, setPlayerName] = useSetState({
        black: 'Player A',
        white: 'Player B',
        modify: false
    });

    useUpdateEffect(() => {
        if (activePos) {
            const [X, Y] = activePos;

            if (X >= 0) {
                setTargetMatrix(targetParser(globalBoard.board[X][Y], activePos, ptsMatrix));
            } else {
                setTargetMatrix(deployTargetParser(globalBoard.board, getCore(X, Y).type, curPlayer));
            }
        } else {
            setTargetMatrix(targetParser(null));
        }
    }, [activePos]);

    useUpdateEffect(() => {
        setPtsMatrix(scoreParser(globalBoard.board));
    }, [globalBoard]);

    useUpdateEffect(() => {
        setBlackScore(_.sumBy(ptsMatrix, col => _.sum(col.map(pts => pts < 0 ? 1 : 0))));
        setWhiteScore(_.sumBy(ptsMatrix, col => _.sum(col.map(pts => pts > 0 ? 1 : 0))));
    }, [ptsMatrix]);

    const selectPiece = (X = 0, Y = 0) => {
        if (X >= 0 && targetMatrix[X][Y] && (globalBoard.board[X][Y]?.type !== CORE || (
            globalBoard.board[X][Y]?.owner !== curPlayer && ownerParser(ptsMatrix[X][Y]) === curPlayer))
        ) {
            const newBoard = _.cloneDeep(globalBoard.board);
            const move = (upgrade = NORMAL) => {
                const movedPiece = newBoard[activePos[0]][activePos[1]];

                if (upgrade !== NORMAL && movedPiece.upgrade === NORMAL) {
                    if (movedPiece.owner === BLACK) {
                        setGlobalBoard(prev => ({ blackRunes: coreAllocParser(prev.blackRunes, movedPiece.type) }));
                    } else if (movedPiece.owner === WHITE) {
                        setGlobalBoard(prev => ({ whiteRunes: coreAllocParser(prev.whiteRunes, movedPiece.type) }));
                    }
                }
                if (newBoard[X][Y]) {
                    if (newBoard[X][Y].owner === BLACK) {
                        setGlobalBoard(prev => ({ blackRunes: coreBackParser(prev.blackRunes, newBoard[X][Y]) }));
                    } else if (newBoard[X][Y].owner === WHITE) {
                        setGlobalBoard(prev => ({ whiteRunes: coreBackParser(prev.whiteRunes, newBoard[X][Y]) }));
                    }
                }

                newBoard[X][Y] = { ...movedPiece, upgrade: upgrade === NORMAL ? movedPiece.upgrade : upgrade };
                newBoard[activePos[0]][activePos[1]] = null;
            };

            if (activePos[0] >= 0) {
                const upgrades = upgradeParser(newBoard[activePos[0]][activePos[1]], newBoard[X][Y], getRunes());

                if (_.sumBy(upgrades, u => u ? 1 : 0) >= 2) {
                    const container = document.createElement('div');
                    const anchor = document.querySelector(`#board-${X}-${Y}`)

                    render((
                        <Upgrader
                            anchor={anchor}
                            upgrades={upgrades}
                            piece={newBoard[activePos[0]][activePos[1]]}
                            selectCallback={(upgrade) => {
                                unmountComponentAtNode(container);
                                move(upgrade);
                                setActivePos(null);
                                setGlobalBoard({ board: newBoard });
                                togglePlayer();
                            }}
                            onClose={() => unmountComponentAtNode(container)}
                        />
                    ), container);
                    return;
                } else {
                    move();
                }
            } else {
                if (curPlayer === BLACK) {
                    const newRunes = _.cloneDeep(globalBoard.blackRunes);

                    newRunes[-(activePos[0]) - 1][activePos[1]].enable = false;
                    setGlobalBoard({ blackRunes: newRunes });
                } else if (curPlayer === WHITE) {
                    const newRunes = _.cloneDeep(globalBoard.whiteRunes);

                    newRunes[-(activePos[0]) - 4][activePos[1]].enable = false;
                    setGlobalBoard({ whiteRunes: newRunes });
                }
                newBoard[X][Y] = { owner: curPlayer, type: getCore(...activePos).type, upgrade: NORMAL };
            }

            setGlobalBoard({ board: newBoard });
            togglePlayer();
        }

        if (activePos) {
            setActivePos(null);
        } else if (getRunePlayer(X) === curPlayer) {
            setActivePos([X, Y]);
        } else if (X >= 0 && globalBoard.board[X][Y]?.owner === curPlayer) {
            setActivePos([X, Y]);
        }
    };

    const getCore = (X = 0, Y = 0) => {
        if (getRunePlayer(X) === BLACK) {
            return globalBoard.blackRunes[-X - 1][Y];
        } else if (getRunePlayer(X) === WHITE) {
            return globalBoard.whiteRunes[-X - 4][Y];
        } else {
            return null;
        }
    };

    const getRunes = () => {
        if (curPlayer === BLACK) {
            return globalBoard.blackRunes;
        } else if (curPlayer === WHITE) {
            return globalBoard.whiteRunes;
        } else {
            return null;
        }
    }

    const selectRune = (rune = [], index = 0) => {
        const newRune = _.cloneDeep(getRunes());
        const newShuffleRunes = _.cloneDeep(shuffleRunes);
        let stageCont = false;

        newRune[_.findIndex(newRune, rune => !rune)] = rune;
        newShuffleRunes[index].forEach(core => {
            core.enable = false;
            core.owner = curPlayer;
        });
        if (curPlayer === BLACK) {
            setGlobalBoard({ blackRunes: newRune });
            stageCont = _.includes(newRune, null) || _.includes(globalBoard.whiteRunes, null);
        } else if (curPlayer === WHITE) {
            setGlobalBoard({ whiteRunes: newRune });
            stageCont = _.includes(newRune, null) || _.includes(globalBoard.blackRunes, null);
        }
        setShuffleRunes(newShuffleRunes);
        if (stageCont) {
            togglePlayer();
        }
    };

    return (
        <EuiFlexGroup
            id='snapshot-zone'
            direction='column'
        >
            <EuiFlexItem id='no-snapshot'>
                <EuiPanel>
                    <EuiFlexGroup
                        wrap={true}
                        alignItems='center'
                    >
                        <EuiFlexItem>
                            <EuiFlexGroup
                                direction='column'
                                gutterSize='s'
                                justifyContent='spaceBetween'
                            >
                                <EuiFlexItem>
                                    <EuiFlexGroup
                                        direction='row'
                                        gutterSize='s'
                                        alignItems='center'
                                    >
                                        <EuiFlexItem grow={false}>
                                            <EuiImage
                                                src={Logo}
                                                alt='Exstal Logo'
                                                size={Styles.iconSize}
                                            />
                                        </EuiFlexItem>
                                        <EuiFlexItem grow={false}>
                                            <EuiTitle size='m'>
                                                <h2>Exstal Arena</h2>
                                            </EuiTitle>
                                        </EuiFlexItem>
                                        <EuiFlexItem grow={false}>
                                            <EuiBetaBadge
                                                label='Beta'
                                                tooltipContent='Offline testing of basic functions and rules, not including online functions.'
                                            />
                                        </EuiFlexItem>
                                    </EuiFlexGroup>
                                </EuiFlexItem>
                                <EuiFlexItem>
                                    <EuiFlexGroup>
                                        <EuiFlexItem grow={false}>
                                            <EuiRadio
                                                label='Black Turn'
                                                id='black-radio'
                                                checked={curPlayer === BLACK}
                                                onChange={() => togglePlayer(BLACK)}
                                                disabled={done}
                                            />
                                        </EuiFlexItem>
                                        <EuiFlexItem grow={false}>
                                            <EuiRadio
                                                label='White Turn'
                                                id='white-radio'
                                                checked={curPlayer === WHITE}
                                                onChange={() => togglePlayer(WHITE)}
                                                disabled={done}
                                            />
                                        </EuiFlexItem>
                                    </EuiFlexGroup>
                                </EuiFlexItem>
                                <EuiFlexItem>
                                    <EuiSwitch
                                        label='Game Done'
                                        checked={done}
                                        onChange={() => doneCtl.toggle()}
                                    />
                                </EuiFlexItem>
                            </EuiFlexGroup>
                        </EuiFlexItem>
                        <EuiFlexItem className='control-bar'>
                            <EuiKeyPadMenuItem
                                label='Undo'
                                disabled={true}
                            >
                                <EuiIcon type='upgradeAssistantApp' size='xl' />
                            </EuiKeyPadMenuItem>
                            <EuiKeyPadMenuItem
                                label='Load'
                                onClick={() => uploadFile(res => {
                                    const { runes, global } = JSON.parse(res);

                                    setGlobalBoard(global);
                                    setShuffleRunes(runes);
                                })}
                            >
                                <EuiIcon type='indexRollupApp' size='xl' />
                            </EuiKeyPadMenuItem>
                            <EuiKeyPadMenuItem
                                label='Save'
                                onClick={() => {
                                    const blob = new Blob(
                                        [JSON.stringify({ runes: shuffleRunes, global: globalBoard })],
                                        { type: 'text/plain' }
                                    );

                                    downloadFile(`${playerName.black} vs ${playerName.white}.ets`, blob);
                                }}
                            >
                                <EuiIcon type='savedObjectsApp' size='xl' />
                            </EuiKeyPadMenuItem>
                            <EuiKeyPadMenuItem
                                label='Players'
                                onClick={() => setPlayerName({ modify: true })}
                            >
                                <EuiIcon type='usersRolesApp' size='xl' />
                            </EuiKeyPadMenuItem>
                            <EuiKeyPadMenuItem
                                label='Step List'
                                disabled={true}
                            >
                                <EuiIcon type='casesApp' size='xl' />
                            </EuiKeyPadMenuItem>
                            <EuiKeyPadMenuItem
                                label='Snapshot'
                                onClick={() => {
                                    const node = document.getElementById('snapshot-zone');
                                    const scoreBar = document.getElementById('snapshot-score-bar');
                                    const board = document.getElementById('snapshot-board');

                                    domtoimage.toBlob(node, {
                                        filter: node => {
                                            if (node.id === 'no-snapshot') {
                                                return false;
                                            } else if (node.tagName === 'IMG' && !node.getAttribute('src')) {
                                                return false;
                                            } else {
                                                return true;
                                            }
                                        },
                                        bgcolor: Styles.white,
                                        height: scoreBar.scrollHeight + board.scrollHeight,
                                        cacheBust: true
                                    }).then(blob => navigator.clipboard.write([new ClipboardItem({
                                        [blob.type]: blob
                                    })]));
                                }}
                            >
                                <EuiIcon type='grokApp' size='xl' />
                            </EuiKeyPadMenuItem>
                        </EuiFlexItem>
                    </EuiFlexGroup>
                </EuiPanel>
            </EuiFlexItem>
            <EuiFlexItem id='snapshot-score-bar'>
                <ScoreBar
                    blackName={playerName.black}
                    whiteName={playerName.white}
                    currentPlayer={curPlayer}
                    blackScore={blackScore}
                    whiteScore={whiteScore}
                    isDone={done}
                />
            </EuiFlexItem>
            <EuiFlexItem id='snapshot-board'>
                <EuiFlexGroup gutterSize='m'>
                    <EuiFlexItem grow={2}>
                        <EuiFlexGroup
                            direction='column'
                            gutterSize='m'
                        >
                            <EuiFlexItem grow={false}>
                                <Timer
                                    currentPlayer={curPlayer}
                                    player={BLACK}
                                    locked={done}
                                />
                            </EuiFlexItem>
                            {globalBoard.blackRunes.map((rune, i) => (
                                <EuiFlexItem
                                    grow={false}
                                    key={i}
                                >
                                    <Rune
                                        owner={BLACK}
                                        cores={rune}
                                        active={activePos && activePos[0] === -(i + 1) ? activePos[1] : null}
                                        onClick={(index) => !done && selectPiece(-(i + 1), index)}
                                    />
                                </EuiFlexItem>
                            ))}
                        </EuiFlexGroup>
                    </EuiFlexItem>
                    {_.includes(globalBoard.blackRunes, null) || _.includes(globalBoard.whiteRunes, null) ? (
                        <>
                            <EuiFlexItem grow={1} />
                            <EuiFlexItem grow={7}>
                                <EuiFlexGrid
                                    direction='row'
                                    columns={3}
                                    responsive={false}
                                    gutterSize='xl'
                                >
                                    {shuffleRunes.map((rune, i) => (
                                        <EuiFlexItem key={i}>
                                            <Rune
                                                owner={_.head(rune)?.owner}
                                                cores={rune}
                                                onClick={() => !(_.head(rune).owner) && selectRune(rune, i)}
                                                general={true}
                                            />
                                        </EuiFlexItem>
                                    ))}
                                </EuiFlexGrid>
                            </EuiFlexItem>
                            <EuiFlexItem grow={1} />
                        </>
                    ) : (
                        <EuiFlexItem grow={9}>
                            <EuiFlexGroup
                                direction='row'
                                responsive={false}
                                gutterSize='s'
                            >
                                {globalBoard.board.map((col, i) => (
                                    <EuiFlexItem key={i}>
                                        {col.map((piece, j) => (
                                            <EuiFlexGroup
                                                direction='column'
                                                responsive={false}
                                                gutterSize='s'
                                                key={j}
                                            >
                                                <EuiFlexItem>
                                                    <Piece
                                                        id={`board-${i}-${j}`}
                                                        piece={piece}
                                                        active={activePos && i === activePos[0] && j === activePos[1]}
                                                        target={targetMatrix[i][j]}
                                                        onClick={() => !done && selectPiece(i, j)}
                                                        owner={ownerParser(ptsMatrix[i][j])}
                                                        currentPlayer={curPlayer}
                                                    />
                                                    <EuiSpacer size='s' />
                                                </EuiFlexItem>
                                            </EuiFlexGroup>
                                        ))}
                                    </EuiFlexItem>
                                ))}
                            </EuiFlexGroup>
                        </EuiFlexItem>
                    )}
                    <EuiFlexItem grow={2}>
                        <EuiFlexGroup
                            direction='column'
                            gutterSize='m'
                        >
                            <EuiFlexItem grow={false}>
                                <Timer
                                    currentPlayer={curPlayer}
                                    player={WHITE}
                                    locked={done}
                                />
                            </EuiFlexItem>
                            {globalBoard.whiteRunes.map((rune, i) => (
                                <EuiFlexItem
                                    grow={false}
                                    key={i}
                                >
                                    <Rune
                                        owner={WHITE}
                                        cores={rune}
                                        active={activePos && activePos[0] === -(i + 4) ? activePos[1] : null}
                                        onClick={(index) => !done && selectPiece(-(i + 4), index)}
                                    />
                                </EuiFlexItem>
                            ))}
                        </EuiFlexGroup>
                    </EuiFlexItem>
                </EuiFlexGroup>
            </EuiFlexItem>
            {playerName.modify && (
                <EuiModal onClose={() => setPlayerName({ modify: false })}>
                    <EuiModalHeader>
                        <EuiModalHeaderTitle>
                            <h3>Modify Players</h3>
                        </EuiModalHeaderTitle>
                    </EuiModalHeader>
                    <EuiModalBody>
                        <EuiFieldText
                            placeholder='Name of Black Side Player'
                            value={playerName.black}
                            onChange={e => setPlayerName({ black: e.target.value })}
                        />
                        <EuiSpacer size='m' />
                        <EuiFieldText
                            placeholder='Name of White Side Player'
                            value={playerName.white}
                            onChange={e => setPlayerName({ white: e.target.value })}
                        />
                    </EuiModalBody>
                    <EuiModalFooter>
                        <EuiButtonEmpty onClick={() => setPlayerName({ modify: false })}>
                            Comfirm
                        </EuiButtonEmpty>
                    </EuiModalFooter>
                </EuiModal>
            )}
        </EuiFlexGroup>
    );
};

export default HomePage;